import React from 'react'
import { Card, Nav, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

const Header = () => {

  /**  */
  const handleLogout = () => {
    console.log('logout')

    // sessionStorage.removeItem('user)
    // ==> "undefined"

    sessionStorage.setItem('user', null);
    window.location.href = "/main"

  }

  return (
    <Card.Header>
      <Nav variant="tabs">
        <Link to="/main">
          <Button variant='light'>Main</Button>
        </Link>


        {JSON.parse(sessionStorage.getItem('user')) == null ?
          (
            /*  - ,  */
            < div >

              <Link to="/join">
                <Button variant='light'></Button>
              </Link>
              <Link to="/login">
                <Button variant='light'></Button>
              </Link>
            </div>

          )
          : (

            /*  - , , , */
            <div>

              <Link to="/memberlist">
                <Button variant='light'></Button>
              </Link>
              <Link to="/mypage">
                <Button variant='light'></Button>
              </Link>
              <Link to="/delete">
                <Button variant='light'></Button>
              </Link>
            </div>

          )}



        {/* Case 1) Session   ?
        - node routes    
        */}
        {/* <Link to="#"> */}

        {/* Case 2) Session   ?  */}
        <Button variant='light' onClick={handleLogout}></Button>
        {/* </Link> */}
      </Nav>
    </Card.Header >
  )
}

export default Header